import { createRouter, createWebHistory } from 'vue-router';
import pluginRoutes from '../extensions/loader';
import storageManager from '@/plugins/storage';
import { translate } from '@/plugins/language.js';
import debug from '@/plugins/Logger.js';

// Initialize empty routes array
const routes = [];

// Cache for frequently accessed data
let configCache = null;
let sessionCache = null;
let cacheTimestamp = 0;
const CACHE_DURATION = 5000; // 5 seconds cache

// Helper function to get cached storage data
function getCachedStorageData() {
  const now = Date.now();
  if (!configCache || !sessionCache || (now - cacheTimestamp) > CACHE_DURATION) {
    configCache = storageManager.get('configs') || {};
    sessionCache = storageManager.get('session') || {};
    cacheTimestamp = now;
  }
  return { configs: configCache, session: sessionCache };
}

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
  scrollBehavior(to, from, savedPosition) {
    // Improved scroll behavior with smooth scrolling
    if (savedPosition) {
      return savedPosition;
    } else if (to.hash) {
      return { el: to.hash, behavior: 'smooth' };
    } else {
      const appElement = document.getElementById('app');
      if (appElement) {
        appElement.scrollIntoView({ behavior: 'smooth' });
      }
      return { top: 0, behavior: 'smooth' };
    }
  },
});

// Optimized plugin route addition
if (pluginRoutes && Array.isArray(pluginRoutes)) {
  pluginRoutes.forEach(route => {
    if (route && typeof route === 'object') {
      router.addRoute(route);
    }
  });
}

router.beforeEach((to, from, next) => {
  try {
    // Prevent infinite redirection
    if (from.path === to.path) {
      debug.log("Preventing infinite redirection from", from.path, "to", to.path);
      next();
      return;
    }

    // Use cached storage data for better performance
    const { configs, session } = getCachedStorageData();
    
    // Extract session data
    const userRole = session.role;
    const userUI = session.interface;
    const isAuthenticated = session.login;
    
    // Extract route meta data with safe access
    const routeRole = to.meta?.role;
    const requiresAuth = to.matched.some(record => record.meta?.auth === true);
    const isAuthOptional = to.meta?.auth === false;
    
    // Plugin validation
    const activePlugins = configs.plugins || [];
    const isRoleValid = !routeRole || (Array.isArray(routeRole) ? routeRole.includes(userRole) : routeRole === userRole);
    
    // Optimized plugin route check
    const routeName = to.name?.toLowerCase();
    const isPluginRoute = routeName ? activePlugins.some(plugin => 
      routeName.startsWith(plugin.toLowerCase())
    ) : false;

    // Enhanced debug logging with consistent formatting
    debug.log("==== Router Navigation Debug ====");
    debug.log("From:", from.path, "To:", to.path, "", to.name);
    debug.log("Auth Required:", requiresAuth);
    debug.log("User Authenticated:", isAuthenticated);
    debug.log("User Role:", userRole, "| User Interface:", userUI);
    debug.log("Plugin Route:", isPluginRoute);
    debug.log("Active Plugins:", activePlugins);
    debug.log("Role Valid:", isRoleValid);

    // Translation logic with safe meta access
    if (to.meta?.parent && to.meta?.key) {
      to.translate = translate(`router-${to.meta.parent}-${to.meta.key}`);
      debug.log("Route Translation:", to.translate);
    }

    debug.log("==================================");

    // Improved routing logic with clearer conditions and infinite loop prevention
    if (requiresAuth && !isAuthenticated) {
      debug.log("Redirect: Authentication required but user not authenticated");
      // Prevent redirecting to the same path
      if (to.path !== '/') {
        updateDocumentMetadata(to, from);
        return next('/');
      } else {
        // If already at home and not authenticated, allow it (might be login page)
        updateDocumentMetadata(to, from);
        return next();
      }
    } 
    
    if (isPluginRoute && configs.siteType !== 'unit' && !isAuthenticated && to.name !== 'member-login') {
      debug.log("Redirect: Plugin route access requires authentication");
      const redirectPath = requiresAuth ? '/member/login' : '/';
      // Prevent redirecting to the same path
      if (to.path !== redirectPath) {
        updateDocumentMetadata(to, from);
        return next(redirectPath);
      } else {
        updateDocumentMetadata(to, from);
        return next();
      }
    } 
    
    if (isPluginRoute && (
      (userUI === 'backend' && isRoleValid) || 
      isAuthOptional || 
      isAuthenticated
    )) {
      debug.log("Allow: Plugin route access granted");
      updateDocumentMetadata(to, from);
      return next();
    } 
    
    if (!isPluginRoute && (!requiresAuth || isAuthenticated)) {
      debug.log("Allow: Non-plugin route access granted");
      updateDocumentMetadata(to, from);
      return next();
    } 
    
    // Final fallback
    debug.log("Deny: Access denied, checking redirect");
    // Prevent redirecting to the same path
    if (to.path !== '/') {
      updateDocumentMetadata(to, from);
      return next('/');
    } else {
      // If already at home, allow it to prevent infinite loop
      debug.log("Already at home, allowing navigation to prevent infinite loop");
      updateDocumentMetadata(to, from);
      return next();
    }
    
  } catch (error) {
    debug.error("Router navigation error:", error);
    // Fallback to allow navigation in case of errors
    updateDocumentMetadata(to, from);
    return next();
  }
});

// Helper function to manage document metadata
function updateDocumentMetadata(to, from) {
  const nearestWithTitle = to.matched
    .slice()
    .reverse()
    .find(r => r.meta?.title);
    
  const nearestWithMeta = to.matched
    .slice()
    .reverse()
    .find(r => r.meta?.metaTags);
    
  const previousNearestWithMeta = from.matched
    .slice()
    .reverse()
    .find(r => r.meta?.metaTags);

  // Update document title
  if (nearestWithTitle?.meta?.title) {
    document.title = nearestWithTitle.meta.title;
  } else if (previousNearestWithMeta?.meta?.title) {
    document.title = previousNearestWithMeta.meta.title;
  }

  // Clean up existing router-controlled meta tags
  const existingTags = document.querySelectorAll('[data-vue-router-controlled]');
  existingTags.forEach(el => el.parentNode?.removeChild(el));

  // Add new meta tags if available
  if (nearestWithMeta?.meta?.metaTags) {
    const fragment = document.createDocumentFragment();
    
    nearestWithMeta.meta.metaTags.forEach(tagDef => {
      const tag = document.createElement('meta');
      Object.entries(tagDef).forEach(([key, value]) => {
        tag.setAttribute(key, value);
      });
      tag.setAttribute('data-vue-router-controlled', '');
      fragment.appendChild(tag);
    });
    
    document.head.appendChild(fragment);
  }
}

export default router;
